<section id="conclusion">
  <h1 class="body-header">What to takeaway:</h1>
  <p class="body-text">
    Perhaps, industry plants may very well be real, present and thriving in the current music industry. 
    Despite this, the volume at which the label gets thrown at upcoming artists is unfair and truly 
    unrepresentative of the talent that leads to some of these artists growth. Through our first visual,
    one understands that a lot of the common industry plants are not experiencing insane amounts of growth
    from recieving many features from spotify. With our second visualization, they can see that both main 
    stream artists and "industry plants" that get this backing from spotify do not experience an insane amount
    of growth as a result. Even if one does not believe that spotify data is represenatitive of an artist's 
    success, there are numerous articles online that also agree with our point of the industry plant label 
    being dated and misused. The term takes away from artists who genuinely grew with their own merits.
    With that, we'd like to leave you with a quote from a Complex article.
  </p>
</section>

<section id="quote-section">
  <p class="quote-text">
    "If it was so easy to pick a random artist and 'plant' them, the industry would be doing it all the time."
  </p>
</section>

