<script>
  import { onMount } from "svelte";
  import { select } from "d3-selection";
  import { scaleTime, scaleLinear } from "d3-scale";
  import { axisBottom, axisLeft } from "d3-axis";
  import { timeParse, timeFormat } from "d3-time-format";
  import { extent } from "d3-array";

  let width = 800;
  let height = 400;
  let margin = { top: 20, right: 30, bottom: 40, left: 70 };
  let drakeData = [];
  let kidLaroiData = [];
  let tooltip; 

  const parseDate = timeParse("%Y-%m-%d");
  const formatDate = timeFormat("%m/%d");

  onMount(async () => {
    try {
      const response = await fetch("CLEANED_featured_Spotify_artist_info.csv");
      const text = await response.text();
      const allData = parseCSV(text);
      
      drakeData = calculatePercentageChanges(filterData(allData, "drake"));
      kidLaroiData = calculatePercentageChanges(filterData(allData, "the kid laroi"));

      console.log("Drake Data:", drakeData);
      console.log("The Kid Laroi Data:", kidLaroiData);

      createChart(drakeData, kidLaroiData);
    } catch (error) {
      console.error("Error loading or processing data:", error);
    }
  });

  function parseCSV(text) {
    const rows = text.split("\n").slice(1);
    return rows.map((row) => {
      const columns = row.split(",");
      if (columns.length > 3) {
        const name = columns[2].trim().toLowerCase();
        const dateStr = columns[0].trim();
        let listeners = columns[3].trim();
        listeners = +listeners.replace(/[^0-9]/g, "");
        const date = parseDate(dateStr);
        return { name, date, listeners };
      }
      return null;
    }).filter(d => d !== null);
  }

  function filterData(data, artist) {
    return data.filter(d => d.name === artist.toLowerCase());
  }

  function calculatePercentageChanges(data) {
    return data.map((d, i) => {
      if (i === 0) return { ...d, percentageChange: 0 };
      const previous = data[i - 1].listeners;
      const change = ((d.listeners - previous) / previous) * 100;
      return { ...d, percentageChange: change, previousListeners: previous };
    });
  }

  function createChart(drakeData, kidLaroiData) {
    if (!drakeData.length && !kidLaroiData.length) {
      console.log("No data available for chart.");
      return;
    }

    const svg = select("#combined-chart")
      .attr("width", width)
      .attr("height", height)
      .style("background-color", "#2c2c2c") 
      .html("")
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    const x = scaleTime()
      .domain(extent([...drakeData, ...kidLaroiData], d => d.date))
      .range([0, width - margin.left - margin.right]);

    const y = scaleLinear()
      .domain([
        Math.min(0, Math.floor(Math.min(...[...drakeData, ...kidLaroiData].map(d => d.percentageChange)))),
        Math.ceil(Math.max(...[...drakeData, ...kidLaroiData].map(d => d.percentageChange)))
      ])
      .range([height - margin.top - margin.bottom, 0]);

    svg.append("g")
      .attr("transform", `translate(0,${height - margin.top - margin.bottom})`)
      .call(axisBottom(x).tickFormat(formatDate))
      .attr("class", "x-axis");

    svg.append("g")
      .call(axisLeft(y).tickFormat(d => `${d}%`))
      .attr("class", "y-axis");

    svg.append("line")
      .attr("x1", 0)
      .attr("x2", width - margin.left - margin.right)
      .attr("y1", y(0))
      .attr("y2", y(0))
      .attr("stroke", "white")
      .attr("stroke-width", 1)
      .style("stroke-dasharray", "4,4");

    svg.selectAll(".drake-bar")
      .data(drakeData)
      .enter()
      .append("rect")
      .attr("class", "drake-bar")
      .attr("x", d => x(d.date) - 10) 
      .attr("y", d => y(Math.max(0, d.percentageChange)))
      .attr("width", 8)
      .attr("height", d => Math.abs(y(d.percentageChange) - y(0)))
      .style("fill", "#66cdaa") 
      .on("mouseover", (event, d) => showTooltip(event, d, "Drake"))
      .on("mouseout", hideTooltip);

    svg.selectAll(".kid-laroi-bar")
      .data(kidLaroiData)
      .enter()
      .append("rect")
      .attr("class", "kid-laroi-bar")
      .attr("x", d => x(d.date) + 2) 
      .attr("y", d => y(Math.max(0, d.percentageChange)))
      .attr("width", 8) 
      .attr("height", d => Math.abs(y(d.percentageChange) - y(0)))
      .style("fill", "#2e8b57") 
      .on("mouseover", (event, d) => showTooltip(event, d, "The Kid Laroi"))
      .on("mouseout", hideTooltip);

    svg.append("text")
      .attr("transform", "rotate(-90)")
      .attr("y", -margin.left + 15)
      .attr("x", -height / 2 + margin.top)
      .attr("dy", "1em")
      .style("text-anchor", "middle")
      .style("fill", "white")
      .text("Percentage Change (%)");

    svg.append("text")
      .attr("transform", `translate(${(width - margin.left - margin.right) / 2},${height - margin.top})`)
      .style("text-anchor", "middle")
      .style("fill", "white")
      .text("Date");

    const legend = select("#legend")
      .style("display", "flex")
      .style("align-items", "center")
      .style("gap", "20px")
      .style("margin", "10px");

    legend.append("div")
      .style("width", "12px")
      .style("height", "12px")
      .style("background-color", "#66cdaa"); 

    legend.append("span")
      .style("color", "white")
      .text("Drake");

    legend.append("div")
      .style("width", "12px")
      .style("height", "12px")
      .style("background-color", "#2e8b57"); 

    legend.append("span")
      .style("color", "white")
      .text("The Kid Laroi");

    tooltip = select("#tooltip");
  }

  function showTooltip(event, d, artist) {
    if (!tooltip) return; 
    tooltip.style("display", "block")
      .style("left", (event.pageX + 10) + "px")
      .style("top", (event.pageY - 40) + "px")
      .html(`
        <strong>${artist}</strong><br>
        Previous: ${d.previousListeners.toLocaleString()}<br>
        Current: ${d.listeners.toLocaleString()}<br>
        Change: ${d.percentageChange.toFixed(2)}%
      `);
  }

  function hideTooltip() {
    if (!tooltip) return; 
    tooltip.style("display", "none");
  }
</script>

<h1 style="text-align: center; color: white;">
  Monthly Listeners Percentage Change for Drake and The Kid Laroi
</h1>
<div style="display: flex; justify-content: center;">
  <div id="legend"></div>
</div>
<svg id="combined-chart"></svg>
<div id="tooltip" style="position: absolute; display: none; padding: 10px; background: #333; color: white; border-radius: 4px; pointer-events: none; font-size: 12px;"></div>

<section id="chart-description">
  <h2>About this chart:</h2>
  <p>
    This chart the percentage changes between each time the artist was featured for Drake and The Kid Laroi.
    The reason we chose these two artists was because they were both featured frequently in this timespan, and
    both fall into different categories of being an already established artist versus one being the "industry plant".
    Readers can hover over the bars to see the specific monthly listeners between times where the artist was
    featured and the percentage change over this duration of time.
    After looking at this visualization, we hope that the reader can see that spotify features are not correlated,
    purely being an industry plant, and that even if an artist has connections to be getting backing from 
    Spotify through features, they don't correlate to success, at least through the metric of listeners.
  </p>
</section>

<style>
  svg {
    display: block;
    margin: auto;
  }

  .drake-bar,
  .kid-laroi-bar {
    stroke-width: 0.5;
    stroke: black;
  }

  .x-axis path,
  .y-axis path,
  .x-axis line,
  .y-axis line {
    stroke: white;
  }

  #legend div {
    display: inline-block;
  }

  #legend span {
    display: inline-block;
    margin-right: 10px;
  }

  #chart-description {
    margin-top: 20px;
    color: white;
    text-align: center;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    background-color: #2c2c2c; 
    border-radius: 8px; 
  }

  #chart-description h2 {
    font-family: 'Arial', sans-serif;
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #66cdaa; 
  }

  #chart-description p {
    font-family: 'Arial', sans-serif;
    font-size: 1rem;
    line-height: 1.6;
  }
</style>
